html, body {margin: 0; height: 100%; overflow: hidden} /* https://stackoverflow.com/questions/28411499/disable-scrolling-on-body */

.inline {
  display:flex; 
  flex-direction:row;
}
.editorBox {
  resize: none;
  /* v taken from react-desktop source v */
  line-height: 15px;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", Arial, sans-serif;
  font-size: 13px;
  letter-spacing: 0.4px;
  /* ^ taken from react-desktop source ^ */
  outline: none;
  width: 345px;
  height: 200px;
  border-color: #c8c8c8;
}
.outputBox {
  resize: none;
  margin-top: 5px;
  /* v taken from react-desktop source v */
  line-height: 15px;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", Arial, sans-serif;
  font-size: 11.5px;
  letter-spacing: 0.4px;
  /* ^ taken from react-desktop source ^ */
  outline: none;
  width: 315px;
  height: 80px;
  color: #be4039;
  border-color: #c8c8c8;
}

.editorBox::-webkit-scrollbar, .outputBox::-webkit-scrollbar {
  display: none;
}

